


























@import '@design';
.md {
  ul:first-of-type {
    display: flex;
    justify-content: flex-start;
    padding: 0;
    margin: 0;
    li {
      margin-right: 10px;
      list-style-type: none;
      a {
        color: $color-nav-link-text;
      }
    }
  }
  img {
    padding: 10px 0;
    filter: drop-shadow(0 0 0.25rem #000);
  }
}
